var web3=require("./web3"),abi=require("./abi"),ETH_METHOD_SIGNATURE_LENGTH=1,contract=function(address,desc){var inputParser=abi.inputParser(desc),outputParser=abi.outputParser(desc),contract={};return desc.forEach(function(method){contract[method.name]=function(){var params=Array.prototype.slice.call(arguments),parsed=inputParser[method.name].apply(null,params),onSuccess=function(result){return outputParser[method.name](result)};return{call:function(extra){return extra=extra||{},extra.to=address,abi.methodSignature(desc,method.name).then(function(signature){return extra.data=signature.slice(0,2+2*ETH_METHOD_SIGNATURE_LENGTH)+parsed,web3.eth.call(extra).then(onSuccess)})},transact:function(extra){return extra=extra||{},extra.to=address,abi.methodSignature(desc,method.name).then(function(signature){return extra.data=signature.slice(0,2+2*ETH_METHOD_SIGNATURE_LENGTH)+parsed,web3.eth.transact(extra).then(onSuccess)})}}}}),contract};module.exports=contract;